
.header-product {
    display: grid;
    grid-template-columns: repeat(2,auto);
}

.filter-product {
    width: 100%;
    text-align: right;
    padding-right: 8px;
}


.remove-style {
    text-decoration: none;
    font-style: none;
}

.tutorial { 
    height: 100px;
}


.details {
    font-size: small;
    padding: 10px;
   
}

.details-list {
    list-style: none;
}


.Course-Items {
    background-color: white;
    width: 100%;
    padding: 0px 0px 20px 0px;
    margin:0;
    border: 1px solid rgb(63, 63, 63,0.3);
    border-radius: 5px;
    position: relative;
    text-align: left;
    height: 100%;
}

.Course-Items:hover {
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}

.Profit {
    color:red;
}

.row:hover  {
    box-shadow: 5px rgba(184, 184, 184);
}

.supplier {
    color:rgba(53, 53, 53, 0.719);
    position: absolute;
    bottom: 10px;
}

.img_adjust {
    width: 100%;
    height: 178px;
    display: block;
    justify-content: center;
    
}




.grid {
    width: 100%;
    display: block;
    padding: 100px 0px 100px 0px;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
}

.row.no-gutters {
    margin-left: 0;
    margin-right: 0;
}

.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}

.c-0 {
    display: none;
}

.c-1 {
    flex: 0 0 8.33333%;
    max-width: 8.33333%;
}

.c-2 {
    flex: 0 0 16.66667%;
    max-width: 16.66667%;
}

.c-3 {
    flex: 0 0 25%;
    max-width: 25%;
}

.c-4 {
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
}

.c-5 {
    flex: 0 0 41.66667%;
    max-width: 41.66667%;
}

.c-6 {
    flex: 0 0 50%;
    max-width: 50%;
}

.c-7 {
    flex: 0 0 58.33333%;
    max-width: 58.33333%;
}

.c-8 {
    flex: 0 0 66.66667%;
    max-width: 66.66667%;
}

.c-9 {
    flex: 0 0 75%;
    max-width: 75%;
}

.c-10 {
    flex: 0 0 83.33333%;
    max-width: 83.33333%;
}

.c-11 {
    flex: 0 0 91.66667%;
    max-width: 91.66667%;
}

.c-12 {
    flex: 0 0 100%;
    max-width: 100%;
}

.c-o-1 {
    margin-left: 8.33333%;
}

.c-o-2 {
    margin-left: 16.66667%;
}

.c-o-3 {
    margin-left: 25%;
