.Login {
    position: fixed;
    top:0; bottom: 0; left:0; right:0;
    z-index: 102;
    width: auto;
    display: block;
    align-items: center;
}

.sign-up {
    position: absolute;
    top : 20px;
    right: 30px;
    color: white;
    font-size: larger;
}



.login-box {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    padding: 580px 0 70px 0;
    background-size: cover;
    background-image:  url(https://lh3.googleusercontent.com/CdidNuvJY8xw61lZbU1miC87DnFbTZmHbXitc6OfJt9vSoiwDOpisd_ih-bMarror9XG3KzISl9KfzzOJOHI3YJSRu1a_5sAMOHZWRc4jyC9FYmxRIqd0AMLFyP6mynox61JzYwLFQ=w2400); 
}

.login-center {
    border: none;
    text-align: center;
    height: 100%;
    align-items: center;
}

.login-form {
    display: grid;
    grid-template-columns: repeat(3,auto);
    grid-column-gap: 10px;

}

.form-control {
    border-radius: 10px;
}

.hide-login {
    display: none;
}



/* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {
    .login-box {
        background-image: url(https://lh3.googleusercontent.com/4IAugQxzPADMt4Icgnq0WilKzPdpynq-9tVpWmT_3IF94lroM36XFKVkjbjCOYxAoiawFP5S37YrkS3txEFB8d6BKUquvGOLw0Yfkg5h6cRBwVQvD-CNoVeE0a5DGBl3_GUkiTO-7A=w2400);
        background-size: cover;   
        padding-top: 400px;  
    }

    .login-form {
        display: grid;
        grid-template-columns: repeat(1,auto);
        gap: 10px;
        width: 100%;
    }    

    .login-center {
        height: 500px;
    }

}

/* Tablet:  width >=740px  and width < 1024px */
@media only screen and (max-width:63.9375em) and (min-width:46.25em) {
    .login-box {
        background-image: url(https://lh3.googleusercontent.com/4IAugQxzPADMt4Icgnq0WilKzPdpynq-9tVpWmT_3IF94lroM36XFKVkjbjCOYxAoiawFP5S37YrkS3txEFB8d6BKUquvGOLw0Yfkg5h6cRBwVQvD-CNoVeE0a5DGBl3_GUkiTO-7A=w2400);
        background-size: cover;   
        padding-top: 400px;  
    }

    .login-form {
        display: grid;
        grid-template-columns: repeat(1,auto);
        gap: 10px;
        width: 100%;
    }    

    .login-center {
        height: 500px;
    }
    
}