.bag {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    text-align: center;
}

.orderproduct {
    background-color: white;
    border-radius: 5px;
    padding: 50px 70px 100px 70px;
    width: 100%;
    font-size: medium;
    position: relative;

}

.champion {
    width: 50%;
}


.order-content {
    padding: 20px;
}


.order-items {
  display: block;
}

.card {
    box-shadow: 12px 12px 2px 1px rgba(199, 199, 199, 0.2);
}

.try {
    display: grid;
    grid-template-columns: repeat(2,auto);
    
}

.ele-orders {
    display: block;
    text-align: right;
    font-size: medium;
}

.bg-color {
    color: rgb(127, 136, 255);
    text-decoration: underline;
}



.item-orders-img {
    border: 0.5px solid rgba(189, 189, 189, 0.479);
    border-radius: 5px;
    padding: 5px;
    width: 100px;
}


.cash-back {
    z-index: 2;
    position: absolute;
    right: 30px;
    top: 30px;
}

.withdraw {
    padding: 20px;
}

.confirm-form {
    padding: 10px;
}